'use client';

import React, { useMemo } from 'react';
import Link from 'next/link';
import {
  IconArrowRight,
  IconCalendarEvent,
  IconChevronDown,
  IconClock,
  IconMail,
  IconMapPin,
  IconPhone,
} from '@tabler/icons-react';

import { Button } from '../elements/button';

type AnyInput = {
  type?: string | null;
  name?: string | null;
  placeholder?: string | null;
  options?: Array<{ label?: string; value?: string }> | string[];
};

export function FormNextToSection({
  heading,
  sub_heading,
  form,
  section,
  social_media_icon_links,
}: {
  heading: string;
  sub_heading: string;
  form: any;
  section: any;
  social_media_icon_links: any;
}) {
  const parsed = useMemo(() => {
    const rawInputs: AnyInput[] = Array.isArray(form?.inputs) ? form.inputs : [];

    const normType = (t: any) => String(t || '').toLowerCase().trim();

    const firstOf = (...types: string[]) =>
      rawInputs.find((i) => types.includes(normType(i?.type)));

    const firstNonSubmit = () =>
      rawInputs.find((i) => !['submit'].includes(normType(i?.type)));

    const nameField =
      firstOf('text') ||
      rawInputs.find(
        (i) =>
          !['submit', 'textarea', 'select', 'email', 'tel'].includes(normType(i?.type))
      ) ||
      firstNonSubmit();

    const phoneField = firstOf('tel');
    const selectField = firstOf('select');
    const emailField = firstOf('email');
    const messageField = firstOf('textarea');
    const submitField = firstOf('submit');

    const hasAnyInputs = rawInputs.length > 0;

    const fallback = {
      name: { type: 'text', name: 'Teljes Név', placeholder: 'Kovács Anna' },
      phone: { type: 'tel', name: 'Telefonszám', placeholder: '+36 30 123 4567' },
      service: { type: 'select', name: 'Szolgáltatás', placeholder: 'Hajvágás & styling' },
      email: { type: 'email', name: 'E-mail', placeholder: 'hello@davelopment.hu' },
      message: { type: 'textarea', name: 'Üzenet (Opcionális)', placeholder: 'Írd le miben segíthetünk ...' },
      submit: { type: 'submit', name: 'Üzenet küldése' },
    };

    const pick = <T,>(v: T | undefined | null, fb: T) => (v == null ? fb : v);

    const out = {
      name: {
        type: 'text',
        label: pick(nameField?.name, fallback.name.name),
        placeholder: pick(nameField?.placeholder, fallback.name.placeholder),
      },
      phone: {
        type: 'tel',
        label: pick(phoneField?.name, fallback.phone.name),
        placeholder: pick(phoneField?.placeholder, fallback.phone.placeholder),
      },
      service: {
        type: 'select',
        label: pick(selectField?.name, fallback.service.name),
        placeholder: pick(selectField?.placeholder, fallback.service.placeholder),
        options: (() => {
          const opts = selectField?.options;
          if (Array.isArray(opts) && opts.length) return opts;
          return [
            { label: 'Hajvágás & styling', value: 'Hajvágás & styling' },
            { label: 'Festés / Airtouch', value: 'Festés / Airtouch' },
            { label: 'Balayage', value: 'Balayage' },
            { label: 'Köröm', value: 'Köröm' },
          ];
        })(),
        isFromData: Boolean(selectField),
      },
      email: {
        type: 'email',
        label: pick(emailField?.name, fallback.email.name),
        placeholder: pick(emailField?.placeholder, fallback.email.placeholder),
      },
      message: {
        type: 'textarea',
        label: pick(messageField?.name, fallback.message.name),
        placeholder: pick(messageField?.placeholder, fallback.message.placeholder),
      },
      submit: {
        type: 'submit',
        label: pick(submitField?.name, fallback.submit.name),
        hasSubmit: Boolean(submitField),
      },
      hasAnyInputs,
      hasSelectInData: Boolean(selectField),
    };

    return out;
  }, [form]);

  const contact = useMemo(() => {
    const address =
      section?.address ||
      section?.cim ||
      section?.location ||
      '1052 Budapest,\nPélda utca 12.';
    const phone = section?.phone || section?.telefon || '+36 1 234 5678';
    const email = section?.email || section?.email_address || 'info@lazarsalon.com';

    return { address, phone, email };
  }, [section]);

  const fieldLabelClass = 'block text-sm font-medium text-[#4f4c4c]';
  const fieldBaseClass =
    'block w-full rounded-md bg-[#f5f5f5] px-4 py-3 text-sm text-[#292624] placeholder:text-[#4f4c4c]/40 outline-none ring-1 ring-black/5 focus:ring-2 focus:ring-[#B2933D]/30';
  const cardClass = 'rounded-2xl bg-white shadow-sm border border-black/5';

  return (
    <section className="w-full bg-[#f5f5f5]">
      <div className="mx-auto max-w-6xl px-4 sm:px-6 lg:px-8 py-16 lg:py-20">
        <div className="text-center">
          <div className="flex items-center justify-center gap-3 text-xs uppercase tracking-[0.22em] text-[#4f4c4c]/80">
            <span className="h-px w-10 bg-[#B2933D]/40" />
            <span>Lépjen velünk kapcsolatba</span>
            <span className="h-px w-10 bg-[#B2933D]/40" />
          </div>

          <h1 className="mt-6 text-3xl sm:text-4xl font-semibold tracking-tight text-[#292624]">
            {heading}
          </h1>

          {sub_heading ? (
            <p className="mx-auto mt-4 max-w-2xl text-sm sm:text-base text-[#4f4c4c]">
              {sub_heading}
            </p>
          ) : null}
        </div>

        <div className="mt-12 grid grid-cols-1 md:grid-cols-2 gap-10 lg:gap-12 items-start">
          {/* LEFT CARD */}
          <div className={`${cardClass} p-6 sm:p-8`}>
            <h2 className="text-xl font-semibold text-[#292624]">Elérhetőségeink</h2>

            <div className="mt-6 space-y-5">
              <div className="flex gap-4">
                <div className="h-10 w-10 rounded-full bg-[#f5f5f5] flex items-center justify-center shrink-0">
                  <IconMapPin className="h-5 w-5 text-[#B2933D]" stroke={1.8} />
                </div>
                <div>
                  <p className="text-xs font-semibold text-[#B2933D]">Címünk</p>
                  <p className="mt-1 text-sm text-[#292624] whitespace-pre-line">
                    {contact.address}
                  </p>
                </div>
              </div>

              <div className="flex gap-4">
                <div className="h-10 w-10 rounded-full bg-[#f5f5f5] flex items-center justify-center shrink-0">
                  <IconPhone className="h-5 w-5 text-[#B2933D]" stroke={1.8} />
                </div>
                <div>
                  <p className="text-xs font-semibold text-[#B2933D]">Telefonszám</p>
                  <p className="mt-1 text-sm text-[#292624]">{contact.phone}</p>
                  <p className="mt-1 text-xs text-[#4f4c4c]/80">
                    Hétfő - Szombat: 09:00 - 18:00
                  </p>
                </div>
              </div>

              <div className="flex gap-4">
                <div className="h-10 w-10 rounded-full bg-[#f5f5f5] flex items-center justify-center shrink-0">
                  <IconMail className="h-5 w-5 text-[#B2933D]" stroke={1.8} />
                </div>
                <div>
                  <p className="text-xs font-semibold text-[#B2933D]">E-mailcím</p>
                  <p className="mt-1 text-sm text-[#292624]">{contact.email}</p>
                </div>
              </div>
            </div>

            <div className="mt-8">
              <div className="flex items-center gap-2 text-sm font-semibold text-[#292624]">
                <IconClock className="h-4 w-4 text-[#B2933D]" stroke={1.8} />
                <span>Nyitvatartás</span>
              </div>

              <div className="mt-4 space-y-3 text-sm">
                <div className="flex items-center justify-between text-[#4f4c4c]">
                  <span>Hétfő - Péntek</span>
                  <span className="text-[#292624]">09:00 - 20:00</span>
                </div>
                <div className="flex items-center justify-between text-[#4f4c4c]">
                  <span>Szombat</span>
                  <span className="text-[#292624]">10:00 - 16:00</span>
                </div>
                <div className="flex items-center justify-between text-[#4f4c4c]">
                  <span>Vasárnap</span>
                  <span className="text-[#B2933D] font-medium">Zárva</span>
                </div>
              </div>
            </div>

            <div
              className="mt-8 h-56 rounded-xl overflow-hidden relative border border-black/5"
              style={{
                backgroundImage:
                  'linear-gradient(0deg, rgba(0,0,0,0.03), rgba(0,0,0,0.03)), radial-gradient(circle at 30% 35%, rgba(178,147,61,0.14), transparent 55%), radial-gradient(circle at 70% 70%, rgba(0,0,0,0.06), transparent 55%)',
              }}
            >
              <div className="absolute inset-0 flex items-center justify-center">
                <div className="flex items-center gap-2 rounded-full bg-white/85 px-4 py-2 shadow-sm border border-black/5">
                  <IconMapPin className="h-4 w-4 text-[#B2933D]" stroke={1.8} />
                  <span className="text-sm font-medium text-[#292624]">Budapest</span>
                </div>
              </div>

              <div className="absolute bottom-3 right-3">
                <Link
                  href="#"
                  className="inline-flex items-center gap-2 rounded-full bg-white px-4 py-2 text-xs font-semibold text-[#292624] shadow-sm border border-black/10 hover:border-black/20"
                >
                  Útvonaltervezés
                </Link>
              </div>
            </div>
          </div>

          {/* RIGHT CARD */}
          <div className={`${cardClass} p-6 sm:p-8`}>
            <div className="flex items-center justify-between gap-4">
              <h2 className="text-xl font-semibold text-[#292624]">Kapcsolatfelvétel</h2>

              <Button
                className="h-9 px-3 rounded-md bg-[#B2933D] text-white hover:opacity-90 shadow-sm"
                type="button"
              >
                <span className="inline-flex items-center gap-2 text-xs font-semibold tracking-wide">
                  <IconCalendarEvent className="h-4 w-4" stroke={2} />
                  IDŐPONTFOGLALÁS
                </span>
              </Button>
            </div>

            <p className="mt-4 text-xs sm:text-sm text-[#4f4c4c]/80 max-w-md">
              Kérjük töltse ki az alábbi űrlapot, és kollégáink hamarosan felveszik Önnel a
              kapcsolatot a véglegesítéshez.
            </p>

            <form className="mt-7 space-y-5">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className={fieldLabelClass} htmlFor="fullName">
                    {parsed.name.label}
                  </label>
                  <div className="mt-2">
                    <input
                      id="fullName"
                      type="text"
                      placeholder={parsed.name.placeholder}
                      className={fieldBaseClass}
                    />
                  </div>
                </div>

                <div>
                  <label className={fieldLabelClass} htmlFor="phone">
                    {parsed.phone.label}
                  </label>
                  <div className="mt-2">
                    <input
                      id="phone"
                      type="tel"
                      placeholder={parsed.phone.placeholder}
                      className={fieldBaseClass}
                    />
                  </div>
                </div>
              </div>

              <div>
                <label className={fieldLabelClass} htmlFor="service">
                  {parsed.service.label}
                </label>
                <div className="mt-2 relative">
                  <select
                    id="service"
                    defaultValue=""
                    className={`${fieldBaseClass} appearance-none pr-10`}
                  >
                    <option value="" disabled>
                      {parsed.service.placeholder}
                    </option>

                    {Array.isArray(parsed.service.options) &&
                      parsed.service.options.map((opt: any, idx: number) => {
                        const label = typeof opt === 'string' ? opt : opt?.label;
                        const value = typeof opt === 'string' ? opt : opt?.value || opt?.label;
                        if (!label) return null;
                        return (
                          <option key={`opt-${idx}`} value={String(value)}>
                            {String(label)}
                          </option>
                        );
                      })}
                  </select>

                  <IconChevronDown
                    className="pointer-events-none absolute right-3 top-1/2 -translate-y-1/2 h-4 w-4 text-[#4f4c4c]/70"
                    stroke={2}
                  />
                </div>
              </div>

              <div>
                <label className={fieldLabelClass} htmlFor="email">
                  {parsed.email.label}
                </label>
                <div className="mt-2">
                  <input
                    id="email"
                    type="email"
                    placeholder={parsed.email.placeholder}
                    className={fieldBaseClass}
                  />
                </div>
              </div>

              <div>
                <label className={fieldLabelClass} htmlFor="message">
                  {parsed.message.label}
                </label>
                <div className="mt-2">
                  <textarea
                    id="message"
                    rows={6}
                    placeholder={parsed.message.placeholder}
                    className={`${fieldBaseClass} resize-none`}
                  />
                </div>
              </div>

              <p className="text-[11px] sm:text-xs text-[#4f4c4c]/75">
                Az űrlap elküldésével hozzájárul adatai kezeléséhez az{' '}
                <Link href="#" className="text-[#B2933D] hover:underline font-medium">
                  Adatvédelmi irányelveink
                </Link>{' '}
                szerint.
              </p>

              <Button
                className="w-full h-11 rounded-md bg-[#B2933D] text-white hover:opacity-90 shadow-sm flex items-center justify-center gap-2"
                type="submit"
              >
                <span className="font-semibold">Üzenet küldése</span>
                <IconArrowRight className="h-4 w-4" stroke={2.2} />
              </Button>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
}
